const AssessRisk = props => {
  const { riskDetails, openDrawer, onCloseDrawer, ...rest } = props;
  const [anchor] = useState('right');
  const [anchorEl, setAnchorEl] = useState(null);
  const [open, setOpen] = useState(false);
  const classes = useStyle();
  const handleClick = event => {
    setAnchorEl(event.currentTarget);
    setOpen(!open);
  };
  const canBeOpen = open && Boolean(anchorEl);
  const id = canBeOpen ? 'spring-popper' : undefined;

  const getColor = () => {
    let color;
    color = riskDetails.status === 'Approved' ? 'green' : 'orange';
    return color;
  };
  return (
    <CmtDrawer open={openDrawer} variant="temporary" anchor={anchor} onClose={onCloseDrawer} {...rest}>
      <div className={clsx(classes.root)}>
        <Box className={classes.contentArea}>
          <PerfectScrollbar className={classes.scrollbarRoot}>
            <Box className={classes.headerRoot}>
              <Box className={classes.headerBgImg}>
                <CmtImage src={'/images/profile-bg-img.png'} />
              </Box>
              <Box className={classes.headerContent}>
                <Box display={'flex'} alignItems={'center'}>
                  <IconButton className={classes.iconBtn} onClick={onCloseDrawer}>
                    <CloseIcon />
                  </IconButton>
                  <Typography variant={'h5'} style={{ color: 'white' }}>
                    {riskDetails.id}
                  </Typography>
                  <Box flex={'1 0 auto'} />
                  <RoleBasedGuard permission={PERMISSIONS.RISK_ASSESSMENT.CREATE}>
                    <Button
                      style={{ color: 'green', borderColor: 'green' }}
                      variant={'contained'}
                      onClick={handleClick}
                      endIcon={<ArrowDropDown />}
                      title={'Click to assess risk'}>
                      Assess Risk
                    </Button>
                  </RoleBasedGuard>

                  {/*<PopupState variant={'popper'} popupId={'popup-popper'}>*/}
                  {/*  {popupState => (*/}
                  {/*    <>*/}
                  {/*      */}
                  {/*      <Popper {...bindPopper(popupState)} placement={'bottom'} transition>*/}
                  {/*        <AssessForm {...{ riskDetails }} />*/}
                  {/*      </Popper>*/}
                  {/*    </>*/}
                  {/*  )}*/}
                  {/*</PopupState>*/}

                  {/*<Chip*/}
                  {/*  size={'small'}*/}
                  {/*  variant={'default'}*/}
                  {/*  label={riskDetails.status}*/}
                  {/*  style={{ color: 'white', backgroundColor: getColor() }}*/}
                  {/*/>*/}
                </Box>
                {open && <AssessForm {...{ riskDetails, setOpen }} />}
              </Box>
            </Box>
            {!open && (
              <Box mb={4}>
                <Preview {...{ riskDetails, isView: true }} />
              </Box>
            )}
          </PerfectScrollbar>
        </Box>
      </div>
    </CmtDrawer>
  );
};
const AssessForm = props => {
  const { riskDetails, setOpen } = props;
  const [updatedRiskDetails, setUpdatedRiskDetails] = useState(riskDetails);
  const classes = useStyles();
  const [riskImpact, setRiskImpact] = useState([]);
  const [riskControls, setRiskControls] = useState([]);

  const onHandleSave = () => {
    NotificationManager.success('Risk assessed successfully!', 'Success!');
    setOpen(false);
  };

  // useEffect(() => {
  //   setUpdatedRiskDetails({ ...updatedRiskDetails, riskImpact: riskImpact, controlActions: riskControls });
  // }, [riskImpact, riskControls]);

  return (
    <Box padding={4} mb={3} className={classes.inBuildAppCard}>
      <Box display={'flex'} mb={3}>
        <Box flex={'1 0 auto'} />
        <Button
          size={'small'}
          onClick={e => onHandleSave()}
          style={{ marginRight: '10px', color: 'white', backgroundColor: 'green' }}>
          Save
        </Button>
        <Button size={'small'} onClick={e => setOpen(false)} style={{ color: 'white', backgroundColor: 'darkred' }}>
          Cancel
        </Button>
      </Box>
      <GridContainer>
        <Grid item md={12} xs={12}>
          <Autocomplete
            multiple
            fullWidth
            value={updatedRiskDetails.riskImpact.length !== 0 ? updatedRiskDetails.riskImpact : riskImpact}
            options={riskImpact}
            disableCloseOnSelect
            filterOptions={(option, params) => {
              const filtered = filter(option, params);
              // Suggest the creation of a new value
              if (params.inputValue !== '') {
                filtered.push(`${params.inputValue}`);
              }
              return filtered;
            }}
            autoHighlight
            onChange={(event, value) => {
              setRiskImpact(value);
              setUpdatedRiskDetails({ ...updatedRiskDetails, riskImpact: value });
            }}
            freeSolo
            selectOnFocus
            clearOnBlur
            getOptionLabel={option => option}
            renderOption={(option, { selected }) => (
              <span key={option}>
                <Checkbox
                  color={'primary'}
                  icon={icon}
                  checkedIcon={checkedIcon}
                  style={{ marginRight: 8 }}
                  checked={selected}
                />
                {option}
              </span>
            )}
            renderInput={params => <TextField fullWidth variant={'outlined'} {...params} label="Risk Impact" />}
          />
        </Grid>
        <Grid item md={12} xs={12}>
          <TextField
            fullWidth
            label="Risk Impact Amount"
            type={'number'}
            variant="outlined"
            value={updatedRiskDetails.riskImpactAmount}
            onChange={e => {
              setUpdatedRiskDetails({ ...updatedRiskDetails, riskImpactAmount: e.target.value });
            }}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <Typography>KES</Typography>
                </InputAdornment>
              ),
            }}
          />
        </Grid>
        <Grid item md={12} xs={12}>
          <AppSelectBox
            fullWidth
            data={lossTypeList}
            label="Loss Type"
            valueKey="name"
            variant="outlined"
            labelKey="name"
            value={updatedRiskDetails.lossType}
            onChange={e => setUpdatedRiskDetails({ ...updatedRiskDetails, lossType: e.target.value })}
          />
        </Grid>
        <Grid item md={12} xs={12}>
          <AppSelectBox
            fullWidth
            data={controlCategory}
            label="Risk Control Category"
            valueKey="name"
            variant="outlined"
            labelKey="name"
            value={updatedRiskDetails.riskControlCat}
            onChange={e => setUpdatedRiskDetails({ ...updatedRiskDetails, riskControlCat: e.target.value })}
          />
        </Grid>
        <Grid item md={12} xs={12}>
          <Autocomplete
            multiple
            fullWidth
            value={updatedRiskDetails.controlActions.length !== 0 ? updatedRiskDetails.controlActions : riskControls}
            options={riskControls}
            disableCloseOnSelect
            filterOptions={(option, params) => {
              const filtered = filter(option, params);
              // Suggest the creation of a new value
              if (params.inputValue !== '') {
                filtered.push(`${params.inputValue}`);
              }
              return filtered;
            }}
            autoHighlight
            onChange={(event, value) => {
              setRiskControls(value);
              setUpdatedRiskDetails({ ...updatedRiskDetails, riskControls: value });
            }}
            freeSolo
            selectOnFocus
            clearOnBlur
            getOptionLabel={option => option}
            renderOption={(option, { selected }) => (
              <span key={option}>
                <Checkbox
                  color={'primary'}
                  icon={icon}
                  checkedIcon={checkedIcon}
                  style={{ marginRight: 8 }}
                  checked={selected}
                />
                {option}
              </span>
            )}
            renderInput={params => (
              <TextField fullWidth variant={'outlined'} {...params} label="Risk Controls and Mitigations" />
            )}
          />
        </Grid>
        <Grid item md={12} xs={12}>
          <AppSelectBox
            fullWidth
            data={probabilityList}
            label="Probability"
            valueKey="name"
            variant="outlined"
            labelKey="name"
            defaultValue={riskDetails.probability}
            value={updatedRiskDetails.probability}
            onChange={e => setUpdatedRiskDetails({ ...updatedRiskDetails, probability: e.target.value })}
          />
        </Grid>
        <Grid item md={12} xs={12}>
          <AppSelectBox
            fullWidth
            data={severityList}
            label="Severity"
            valueKey="name"
            variant="outlined"
            labelKey="name"
            defaultValue={riskDetails.severity}
            value={updatedRiskDetails.severity}
            onChange={e => setUpdatedRiskDetails({ ...updatedRiskDetails, severity: e.target.value })}
          />
        </Grid>
        <Grid item md={12} xs={12}>
          <TextField
            fullWidth
            label="Residual Risk"
            variant="outlined"
            disabled
            value={updatedRiskDetails.residualRisk}
            onChange={e => setUpdatedRiskDetails({ ...updatedRiskDetails, residualRisk: e.target.value })}
          />
        </Grid>
      </GridContainer>
    </Box>
  );
};
